.slogan {
  min-height: 50vh;
  background-color: black;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: aqua;
  padding: 100px 0; }

.container {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.block {
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 50%; }

.sloganCloseTag:before, .sloganOpenTag:before {
  content: "<"; }

.sloganCloseTag:after, .sloganOpenTag:after {
  content: ">"; }

.sloganOpenTag {
  font-family: 'Press Start 2P', cursive;
  margin-bottom: 150px; }

.sloganCloseTag {
  font-family: 'Press Start 2P', cursive;
  margin-top: 150px; }

.title {
  color: white;
  font-family: 'Press Start 2P', cursive;
  margin-bottom: 100px;
  font-weight: 400;
  font-size: 25px; }

.underlined {
  text-decoration: none;
  color: aqua;
  padding-bottom: 0.15em;
  box-sizing: border-box;
  box-shadow: inset 0 -0.2em 0 aqua;
  transition: 0.2s;
  background-color: black; }

.underlined:hover {
  color: #222;
  box-shadow: inset 0 -2em 0 aqua;
  transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1); }

.brk {
  position: relative;
  background: #222;
  color: aqua;
  text-transform: uppercase;
  text-decoration: none;
  border: 0.2em solid aqua;
  padding: 0.5em 1em; }

.brk::before {
  content: "";
  display: block;
  position: absolute;
  width: 10%;
  background: #222;
  height: 0.3em;
  right: 20%;
  top: -0.21em;
  transform: skewX(-45deg);
  -webkit-transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1);
  transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1); }

.brk::after {
  content: "";
  display: block;
  position: absolute;
  width: 10%;
  background: #222;
  height: 0.3em;
  left: 20%;
  bottom: -0.25em;
  transform: skewX(45deg);
  -webkit-transition: all 0.45 cubic-bezier(0.86, 0, 0.07, 1);
  transition: all 0.45s cubic-bezier(0.86, 0, 0.07, 1); }

.brk:hover::before {
  right: 80%; }

.brk:hover::after {
  left: 80%; }

@media (max-width: 905px) {
  .title {
    text-align: center;
    line-height: 1.6; } }
